import React from "react";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Div from "@vkontakte/vkui/dist/components/Div/Div";

const SearchDescription = ({firstName, lastName, personCount, searchButton}) => {

    const declOfNum = (n, text_forms) => {
        n = Math.abs(n) % 100; var n1 = n % 10;
        if (n > 10 && n < 20) { return text_forms[2]; }
        if (n1 > 1 && n1 < 5) { return text_forms[1]; }
        if (n1 == 1) { return text_forms[0]; }
        return text_forms[2];
    }

    return (
        <Div>
            <Div>
                <span>В базе награжденных найдено {personCount} {declOfNum(personCount, ['человек', 'человека', 'человек'])} с фамилией <b>{lastName}</b>.</span>
            </Div>
            <Div>
                <Button size="l" className="SearchButton"
                        title="Посмотреть найденные" onClick={searchButton}>
                    {firstName}, найди своих родственников
                </Button>
            </Div>
        </Div>

    )
};

export default SearchDescription;
