import React from "react";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Search from "@vkontakte/vkui/dist/components/Search/Search";

const SearchBlock = ({isMobileDevice, onLabelChange, handleKeyDown, searchButton}) => {

    const getSearchField = () => {
        let placeHolder = "ФИО, год рождения";

        if (!isMobileDevice()) {
            placeHolder += ", место работы";
        }

        return <Search
            placeholder={placeHolder}
            id="medalSearchId"
            type="text"
            onChange={onLabelChange}
            onKeyDown={handleKeyDown}/>;
    }

    return (
        <Div>
            <Div className="Description">
                Поиск по всей базе награжденных (в базе сейчас 168&nbsp;788 человек)
            </Div>
            {getSearchField()}
            <Div>
                <Button size="l" className="SearchButton"
                        title="Искать на сайте: Медаль «За оборону Ленинграда»" onClick={searchButton}>
                    Искать
                </Button>
            </Div>
        </Div>

    )
};

export default SearchBlock;
